body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#app {
  background: url(../../static/media/bj5.10d2091b.jpeg) no-repeat;
  background-size: 100% 100%; }
  #app .ant-layout {
    background: none; }
  #app .v-header {
    background: #e6f7ff;
    display: flex;
    color: #1890ff;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    opacity: 0.5;
    height: 55px; }
    #app .v-header .trigger {
      font-size: 18px; }
    #app .v-header .header-right {
      color: #1890ff;
      display: flex;
      font-weight: 700;
      flex-direction: column; }
      #app .v-header .header-right span {
        line-height: 20px; }
      #app .v-header .header-right:hover {
        color: #ff7600; }

#Sider {
  background: none;
  color: #fff; }
  #Sider .userInfo {
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center; }
    #Sider .userInfo div {
      flex: 1 1; }
    #Sider .userInfo .userPic img {
      width: 70px;
      height: 70px;
      border-radius: 50%; }
    #Sider .userInfo .userName {
      line-height: 40px; }
    #Sider .userInfo .solgan {
      padding: 5px 10px;
      border-radius: 15px;
      border: 1px solid #fff; }
  #Sider .ant-menu {
    background: none;
    color: #fff; }
    #Sider .ant-menu .ant-menu-submenu-arrow {
      color: #Fff; }

#user .information {
  padding: 10px; }
  #user .information .addButton {
    margin-left: 10px; }
  #user .information .search {
    float: right; }

.item {
  padding: 10px 0; }
  .item input {
    width: 80%; }
  .item span {
    margin: 0 10px; }

.ant-table-wrapper {
  margin: 15px; }

.ant-pagination {
  margin-left: 15px; }

.AddProduct {
  height: 40px;
  line-height: 40px; }
  .AddProduct .icon {
    font-size: 16px;
    margin-right: 5px; }
  .AddProduct span {
    font-size: 16px; }
  .AddProduct button {
    margin: 4px;
    float: right; }

.twoLine {
  padding: 20px 0; }

.cardItem {
  box-shadow: 5px 5px 5px #bbb;
  margin-bottom: 50px; }
  .cardItem .toDetail:hover {
    color: #ff7600; }

.clothes .addButton {
  margin: 10px; }

.clothes .pic {
  width: 150px;
  height: 150px; }

#order .desc {
  height: 40px;
  background: #3975ce;
  line-height: 40px;
  padding: 0 20px;
  color: #fff; }
  #order .desc .descIcon {
    font-size: 30px; }
  #order .desc span {
    font-size: 20px;
    margin-left: 10px; }

#order .orderDetail {
  width: 50%; }
  #order .orderDetail img {
    width: 60px;
    height: auto; }

.login {
  width: 100%; }
  .login .cover {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    background-color: #b8e5f8;
    background-image: url(../../static/media/bj2.f9799d88.jpg); }
  .login .card {
    width: 602px;
    height: 470px;
    border-radius: 50px;
    margin: 0 auto;
    transform: translateY(30%);
    background-color: #fff;
    opacity: 0.8; }
    .login .card .title {
      width: 100%;
      height: 120px;
      padding-top: 20px;
      text-align: center; }
      .login .card .title div {
        height: 50px;
        line-height: 50px;
        display: block; }
        .login .card .title div:nth-of-type(1) {
          font-size: 30px;
          font-weight: 700; }
        .login .card .title div:nth-of-type(2) {
          font-size: 20px;
          font-weight: 400; }
    .login .card .table {
      padding: 0 30px; }
      .login .card .table .inputItem {
        height: 60px;
        display: flex;
        border-bottom: 1px solid #d9d9d9; }
        .login .card .table .inputItem .select {
          flex: 2 1;
          margin: 10px;
          border: none; }
          .login .card .table .inputItem .select .ant-select-selection {
            border: none; }
        .login .card .table .inputItem input {
          height: 59px;
          border: none; }
          .login .card .table .inputItem input:focus {
            box-shadow: none; }
      .login .card .table button {
        width: 80%;
        height: 55px;
        margin: 30px 10% 0;
        text-align: center; }
    .login .card .text {
      color: #b2b2b2; }
    .login .card .footer {
      height: 60px;
      width: 100%;
      background-color: #f6f6f6;
      position: absolute;
      bottom: 0;
      right: 0;
      border: 1px solid #ebebeb;
      line-height: 60px; }
      .login .card .footer span {
        cursor: pointer; }
        .login .card .footer span:nth-of-type(2) {
          color: #0084ff; }

